import React from 'react';
import './TokenInfo.scss';

import classNames from 'classnames';
import {
  fade,
  withStyles,
  makeStyles,
  useTheme,
} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Toolbar from '@material-ui/core/Toolbar';
import InputBase from '@material-ui/core/InputBase';
import SearchIcon from '@material-ui/icons/Search';
import TextField from '@material-ui/core/TextField';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Container from '@material-ui/core/Container';
import LinearProgress from '@material-ui/core/LinearProgress';

import SwipeableViews from 'react-swipeable-views';
import Typography from '@material-ui/core/Typography';
import BigNumber from 'bignumber.js';
import commaNumber from 'comma-number';
import { withConsumer } from '../../Context';

import Holder from './components/Holder';
import History from './components/History';
import Orderbook from './components/Orderbook';
import Config from './components/Config';

import {
  findTokens,
  findMarketMetrics,
  getScotConfig,
  findAllTokenBalance,
} from '../../service/SteemEngineService';

const useStyles = makeStyles(theme => ({
  box: {
    // margin: theme.spacing(1),
    padding: theme.spacing(2),
    height: 'auto',
    display: 'block',
    marginBottom: 0,
  },
}));

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      <Box p={3}>{children}</Box>
    </Typography>
  );
}

const TokenInfo = ({ location, history, match, context }) => {
  // console.log('TokenInfo', { location, history, match });
  const classes = useStyles();
  const theme = useTheme();

  const { symbol } = match.params;

  const [loading, setLoading] = React.useState(true);
  const [token, setToken] = React.useState();
  const [market, setMarket] = React.useState();

  const [selectTab, setSelectTab] = React.useState(0);

  React.useEffect(() => {
    Promise.all([
      findTokens({ query: { symbol } }),
      findMarketMetrics(symbol),
    ]).then(([[_token], [_market]]) => {
      console.log({ _token, _market });
      const metadata = JSON.parse(_token.metadata);
      setToken({
        ..._token,
        ...metadata,
      });
      setMarket(_market);
    });

    setLoading(false);
  }, []);

  return (
    <Container maxWidth="lg" className={classes.container}>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Paper className={classes.root}>
            <div className="ivu-card-body">
              {token && (
                <React.Fragment>
                  <div className="ivu-row">
                    <div className={classNames('ivu-col', 'ivu-col-span-18')}>
                      {token.icon.startsWith('http') && (
                        <img className="token-logo" src={token.icon} alt="" />
                      )}
                      <div className="token-name">{token.symbol}</div>
                      {/* <div class="token-scot-setting" style="display: none;">
                                Cashout Window Days: <strong >0</strong> Day
                            </div>  */}
                      <div className="token-detail">{token.name}</div>
                      <div className="token-detail">
                        {token.url && (
                          <a
                            href={token.url}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="steem-keychain-checked"
                          >
                            {token.url}
                          </a>
                        )}
                      </div>
                      <div className="token-detail">
                        <span>
                          <span style={{ color: '#84929c' }}>issuer:</span>
                          <a
                            href={`https://steemit.com/@${token.issuer}`}
                            className="steem-keychain-checked"
                            target="_blank"
                            rel="noopener noreferrer"
                          >
                            @{token.issuer}
                          </a>
                        </span>
                      </div>
                      <div className="token-detail">{token.desc}</div>
                    </div>
                  </div>
                  <div
                    className={classNames(
                      'ivu-divider',
                      'ivu-divider-horizontal',
                      'ivu-divider-default',
                    )}
                  />
                  <div className="ivu-row" style={{ paddingLeft: 20 }}>
                    {market && (
                      <div className={classNames('ivu-col', 'ivu-col-span-7')}>
                        <div className={classNames('token-price-steem')}>
                          <strong>{market.lastPrice}</strong>{' '}
                          <span className="steem-sign">STEEM</span>
                        </div>
                        <div className="token-price-dollar">
                           $
                          {new BigNumber(market.lastPrice)
                            .times(context.steemPrice)
                            .toFixed(3)}
                        </div>
                        <div className="token-price-detail">
                          {parseFloat(market.priceChangePercent)}%{' '}
                          {parseFloat(market.priceChangeSteem)}
                        </div>
                      </div>
                    )}
                    <div className={classNames('ivu-col', 'ivu-col-span-11')}>
                      <div className="token-supply">
                         Circulating Supply
                        {commaNumber(
                          parseFloat(token.circulatingSupply).toFixed(0),
                        )}{' '}
                        {token.symbol}
                      </div>
                      <div className="token-supply">
                         Max Supply
                        {commaNumber(
                          parseFloat(token.maxSupply).toFixed(0),
                        )}{' '}
                        {token.symbol}
                      </div>
                      <div className="token-supply">
                         Total Staked
                        {commaNumber(token.totalStaked)} {token.symbol}
                      </div>
                      {market && (
                        <div className="token-supply">
                           24H Volume: {market.volume} STEEM
                        </div>
                      )}
                    </div>
                  </div>
                </React.Fragment>
              )}
            </div>
            {token && market ? null : <LinearProgress />}
          </Paper>
        </Grid>
        <Grid item xs={12}>
          <Paper>
            <Toolbar>
              <Tabs
                value={selectTab}
                onChange={(e, v) => setSelectTab(v)}
                indicatorColor="primary"
                // textColor="primary"
                aria-label="full width tabs example"
              >
                <Tab label="Holder" />
                <Tab label="Orerbook" />
                <Tab label="History" />
                <Tab label="Config" />
              </Tabs>
            </Toolbar>
            <SwipeableViews
              axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
              index={selectTab}
              // onChangeIndex={handleChangeIndex}
            >
              <TabPanel value={selectTab} index={0} dir={theme.direction}>
                <Holder
                  symbol={symbol}
                  totalStaked={token && token.totalStaked}
                />
              </TabPanel>
              <TabPanel value={selectTab} index={1} dir={theme.direction}>
                <Orderbook symbol={symbol} />
              </TabPanel>
              <TabPanel value={selectTab} index={2} dir={theme.direction}>
                <History symbol={symbol} />
              </TabPanel>
              <TabPanel value={selectTab} index={3} dir={theme.direction}>
                <Config symbol={symbol} />
              </TabPanel>
            </SwipeableViews>
          </Paper>
        </Grid>
      </Grid>
    </Container>
  );
};

export default withConsumer(TokenInfo);
