import React from 'react';
import PropTypes from 'prop-types';

import clsx from 'clsx';
import { makeStyles, createMuiTheme } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import AccountCircle from '@material-ui/icons/AccountCircle';
import ArrowForward from '@material-ui/icons/KeyboardArrowRight';

import FormControl from '@material-ui/core/FormControl';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

const useStyles = makeStyles(theme => ({
  center: {
    textAlign: 'center',
  },
  button: {
    margin: '10px 10px',
  },
}));

const LoginDialog = ({ open, onClose, onLogin }) => {
  const classes = useStyles();

  const refTextField = React.useRef();

  const [error, setError] = React.useState(false);
  const [username, setUsername] = React.useState('');
  const loginHandler = React.useCallback(() => {
    if (!username || username.length < 3) {
      setError(true);
      // console.log(refTextField);
      refTextField.current.focus();
      return;
    }
    onLogin(username);
  }, [username]);

  React.useEffect(() => {
    setUsername('');
  }, [open]);

  return (
    <Dialog
      open={open}
      keepMounted
      maxWidth="xs"
      fullWidth
      onClose={onClose}
      aria-labelledby="alert-dialog-slide-title"
      aria-describedby="alert-dialog-slide-description"
    >
      <DialogTitle id="alert-dialog-slide-title" className={classes.center}>
        Login to your account
      </DialogTitle>
      <DialogContent dividers className={classes.center}>
        <DialogContentText id="alert-dialog-slide-description">
          Using Steem Keychain
        </DialogContentText>
        <img
          src="https://steemitimages.com/p/2dk2RRM2dZ8gT6jeQvq8mBkwYAePG57tXPiQAWp4MaYC8QpDTR2o7Vktd92nN4g6WnMHHrhGzf1a78KceS1r8MbEyLG21CAK5p7c7VsBQNxtJWUttHL3EjGE2eKCCy5K72pUe3UPvHFajHKFFujUtAMbgEQGKmicSR61rg1jD6"
          alt="keychain"
        />
        <Grid container spacing={1} alignItems="flex-end" justify="center">
          <Grid item>
            <AccountCircle />
          </Grid>
          <Grid item>
            <TextField
              ref={refTextField}
              autoFocus
              margin="dense"
              id="name"
              label="Username"
              type="email"
              fullWidth
              value={username}
              error={error}
              onChange={e => setUsername(e.target.value)}
              onKeyPress={e => {
                if (e.key === 'Enter') {
                  loginHandler();
                  e.preventDefault();
                }
              }}
            />
          </Grid>
        </Grid>
        <div className={classes.button}>
          <Button
            fullWidth
            variant="contained"
            color="primary"
            onClick={loginHandler}
          >
            Login
            <ArrowForward className={classes.extendedIcon} />
          </Button>
        </div>
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}>Cancel</Button>
      </DialogActions>
    </Dialog>
  );
};

LoginDialog.propTypes = {
  open: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onLogin: PropTypes.func.isRequired,
};

export default LoginDialog;
