import React from 'react';

import _ from 'lodash';
import PropTypes from 'prop-types';

import clsx from 'clsx';
import classNames from 'classnames';
import {
  fade,
  withStyles,
  makeStyles,
  useTheme,
} from '@material-ui/core/styles';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Toolbar from '@material-ui/core/Toolbar';
import InputBase from '@material-ui/core/InputBase';
import SearchIcon from '@material-ui/icons/Search';
import TextField from '@material-ui/core/TextField';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Container from '@material-ui/core/Container';
import LinearProgress from '@material-ui/core/LinearProgress';

import SwipeableViews from 'react-swipeable-views';
import Typography from '@material-ui/core/Typography';
import BigNumber from 'bignumber.js';
import commaNumber from 'comma-number';
// import { withConsumer } from '../../Context';

import { __assign } from 'tslib';
import {
  findTokens,
  findMarketMetrics,
  getScotConfig,
  findAllTokenBalance,
} from '../../../service/SteemEngineService';

const useStyles = makeStyles(theme => ({
  box: {
    // margin: theme.spacing(1),
    padding: theme.spacing(2),
    height: 'auto',
    display: 'block',
    marginBottom: 0,
  },
  root: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1),
  },
  spacer: {
    flex: '1 1 100%',
  },
  actions: {
    color: theme.palette.text.secondary,
  },
  title: {
    flex: '0 0 auto',
  },
  tableWrapper: {
    overflowX: 'auto',
  },
}));

const Holder = ({ symbol, totalStaked }) => {
  // console.log('TokenInfo', { totalStaked });
  const classes = useStyles();
  const theme = useTheme();

  const [loading, setLoading] = React.useState(true);
  const [token, setToken] = React.useState();
  const [market, setMarket] = React.useState();
  const [holders, setHolders] = React.useState([]);
  const [orderBy, setOrderBy] = React.useState('balanceStakeSum');
  const [sortOrder, setSortOrder] = React.useState('desc');

  const [selectTab, setSelectTab] = React.useState(0);

  React.useEffect(() => {
    setLoading(true);

    findAllTokenBalance(symbol).then(result => {
      // console.log('findAllTkenBalance', result);
      const holders = result
        .map(holder => {
          const balance = parseFloat(holder.balance || 0);
          const stake = parseFloat(holder.stake || 0);
          const delegationsIn = parseFloat(
            holder.delegationsIn || holder.receivedStake || 0,
          );
          const delegationsOut = parseFloat(
            holder.delegationsOut || holder.delegatedStake || 0,
          );
          const pendingUndelegations = parseFloat(
            holder.pendingUndelegations || 0,
          );
          const pendingUnstake = parseFloat(holder.pendingUnstake || 0);
          const balanceStakeSum = balance + stake + delegationsOut;
          const total =
            balance +
            stake +
            pendingUnstake +
            pendingUndelegations +
            delegationsOut;
          const effectStake = new BigNumber(stake)
            .plus(delegationsIn)
            .toNumber();

          return {
            account: holder.account,
            balance,
            effectStake,
            pendingUnstake,
            pendingUndelegations,
            stake,
            balanceStakeSum,
            delegationsIn,
            delegationsOut,
            total,
          };
        })
        .filter(({ account, total }) => account !== 'null' && total > 0);
      setHolders(holders);
      setLoading(false);
    });
  }, []);

  const holderCount = React.useMemo(() => {
    return holders.length;
  }, [holders]);

  const sortedHolders = React.useMemo(() => {
    return holders
      .sort((a, b) => {
        if (sortOrder === 'desc') return b[orderBy] - a[orderBy];
        return a[orderBy] - b[orderBy];
      })
      .map(e => ({
        ...e,
        effectStakeRate: new BigNumber(e.effectStake)
          .div(totalStaked)
          .times(100),
      }));
    // .sort((a, b) => b[orderBy] - a[orderBy]);
  }, [holders, orderBy, sortOrder, totalStaked]);

  const toogleSortHandeler = val => {
    if (val === orderBy) {
      setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
    } else {
      setOrderBy(val);
      setSortOrder('desc');
    }
  };

  return (
    <React.Fragment>
      <div style={{ paddingLeft: 20, marginBottom: 10 }}>
        <div className="ivu-row">
          <div className={classNames('tit', 'ivu-col', 'ivu-col-span-4')}>
            Accounts
          </div>
          <div className={classNames('val', 'ivu-col', 'ivu-col-span-8')}>
            {holderCount}
          </div>
        </div>
      </div>
      <Divider />
      <div className={classes.tableWrapper}>
        {/* {loading ? <LinearProgress /> : null} */}
        <Table className={classes.table} size="small">
          <TableHead>
            <TableRow>
              <TableCell>No.</TableCell>
              <TableCell>Account</TableCell>
              <TableCell align="right">
                <TableSortLabel
                  active={orderBy === 'balanceStakeSum'}
                  direction={sortOrder}
                  //   direction={order}
                  onClick={() => toogleSortHandeler('balanceStakeSum')}
                >
                  Stake+Balance
                </TableSortLabel>
              </TableCell>
              <TableCell align="right">
                <TableSortLabel
                  active={orderBy === 'stake'}
                  direction={sortOrder}
                  onClick={() => toogleSortHandeler('stake')}
                >
                  Stake
                </TableSortLabel>
              </TableCell>
              <TableCell align="right">
                <TableSortLabel
                  active={orderBy === 'effectStake'}
                  direction={sortOrder}
                  onClick={() => toogleSortHandeler('effectStake')}
                >
                  Effect Stake
                </TableSortLabel>
              </TableCell>
              <TableCell align="right">
                <TableSortLabel
                  active={orderBy === 'balance'}
                  direction={sortOrder}
                  onClick={() => toogleSortHandeler('balance')}
                >
                  Balance
                </TableSortLabel>
              </TableCell>
              {/* <TableCell align="right">
                <TableSortLabel
                  active={orderBy === 'pendingUnstake'}
                  direction={sortOrder}
                  onClick={() => toogleSortHandeler('pendingUnstake')}
                >
                  Pending Unstaking
                </TableSortLabel>
              </TableCell> */}
              {/* <TableCell align="right">Selling</TableCell> */}
              {/* <TableCell align="right">Unstaking</TableCell> */}
              {/* <TableCell align="right">St+Ba</TableCell> */}
              {/* <TableCell align="right">VotingPower</TableCell> */}
              <TableCell align="right">
                <TableSortLabel
                  active={orderBy === 'delegationsOut'}
                  direction={sortOrder}
                  onClick={() => toogleSortHandeler('delegationsOut')}
                >
                  Delegated
                </TableSortLabel>
              </TableCell>
              <TableCell align="right">
                <TableSortLabel
                  active={orderBy === 'delegationsIn'}
                  direction={sortOrder}
                  onClick={() => toogleSortHandeler('delegationsIn')}
                >
                  Received
                </TableSortLabel>
              </TableCell>
              {/* <TableCell align="right">Price</TableCell> */}
            </TableRow>
          </TableHead>
          <TableBody>
            {sortedHolders.map((holder, index) => (
              <TableRow key={holder.account}>
                <TableCell component="th" scope="row">
                  {index + 1}
                </TableCell>
                <TableCell>{holder.account}</TableCell>
                <TableCell align="right">
                  {holder.balanceStakeSum &&
                    commaNumber(holder.balanceStakeSum.toFixed(3))}
                </TableCell>
                <TableCell align="right">
                  {holder.stake && commaNumber(holder.stake.toFixed(3))}
                </TableCell>
                <TableCell align="right">
                  {holder.effectStake &&
                    commaNumber(holder.effectStake.toFixed(3))}{' '}
                  {holder.effectStakeRate
                    ? `(${holder.effectStakeRate.toFixed(3)}%)`
                    : null}
                </TableCell>
                <TableCell align="right">
                  {holder.balance && commaNumber(holder.balance.toFixed(3))}
                </TableCell>
                {/* <TableCell align="right">
                  {holder.pendingUnstake &&
                    commaNumber(holder.pendingUnstake.toFixed(3))}
                </TableCell> */}
                <TableCell align="right">
                  {holder.delegationsOut &&
                    commaNumber(holder.delegationsOut.toFixed(3))}
                </TableCell>
                <TableCell align="right">
                  {holder.delegationsIn &&
                    commaNumber(holder.delegationsIn.toFixed(3))}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
        {loading ? <LinearProgress /> : null}
      </div>
    </React.Fragment>
  );
};

Holder.propTypes = {
  symbol: PropTypes.string.isRequired,
  totalStaked: PropTypes.number.isRequired,
};

export default Holder;
