import React from 'react';
import PropTypes from 'prop-types';

import _ from 'lodash';
import moment from 'moment';
import BigNumber from 'bignumber.js';

import clsx from 'clsx';
// import classNames from 'classnames';
import { fade, makeStyles } from '@material-ui/core/styles';

import Container from '@material-ui/core/Container';
import Toolbar from '@material-ui/core/Toolbar';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import LinearProgress from '@material-ui/core/LinearProgress';
import Divider from '@material-ui/core/Divider';
import Box from '@material-ui/core/Box';

import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import Dialog from '@material-ui/core/Dialog';

import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/InfoRounded';

import HeaderCheckBox from './components/HeaderCheckBox';
import HeaderSerchBox from './components/HeaderSerchBox';

import {
  findMarketMetrics,
  findTokenBalance,
  findTokenDelegations,
} from '../../service/SteemEngineService';
import { withConsumer } from '../../Context';

const useStyles = makeStyles(theme => ({
  spacer: {
    flex: 1,
  },
  head: {
    padding: '0 30px',
    fontSize: '16px',
    backgroundColor: 'rgb(19, 22, 54)',
  },
  upper: {
    fontStyle: 'normal',
    textTransform: 'uppercase',
  },
  tickerTransform: {
    color: '#9194a4',
    fontSize: '12px',
    fontFamily: 'Arial,Helvetica',
    padding: '0 10px',
  },
  legal: {
    display: 'block',
    color: '#9194a4',
    fontSize: '12px',
    fontFamily: 'Arial,Helvetica',
  },
  headOption: {
    padding: '0 20px',
    fontSize: '12px',
  },
  // /
  searchRoot: {
    margin: theme.spacing(1),
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(1),
      width: 'auto',
    },
  },
  searchIcon: {
    width: theme.spacing(7),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 7),
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: 120,
      '&:focus': {
        width: 200,
      },
    },
  },
}));

const fmt = {
  decimalSeparator: '.',
  groupSeparator: ',',
  groupSize: 3,
  secondaryGroupSize: 0,
  fractionGroupSeparator: ' ',
  fractionGroupSize: 0,
};
BigNumber.config({ FORMAT: fmt });

const Wallet = ({ context, match }) => {
  const classes = useStyles();

  const { steemPrice, loginAccount } = context;
  const username = match.params.username || loginAccount;

  const [loading, setLoading] = React.useState(true);
  const [balances, setBalances] = React.useState([]);
  const [totalAssetValue, setTotalAssetValue] = React.useState(0);

  const [orderBy, setOrderBy] = React.useState('balanceStakeSumPrice');
  const [sortOrder, setSortOrder] = React.useState('desc');

  const [isChecked, setIsChecked] = React.useState(false);
  const [search, setSearch] = React.useState();

  const [openDialog, setOpenDialog] = React.useState(false);
  const [dialogItem, setDialogItem] = React.useState();

  // did mounted components
  React.useEffect(() => {
    if (username && !balances.length) {
      (async () => {
        // ,   
        const [resultBalances, resultDelegations] = await Promise.all([
          findTokenBalance({ account: username }),
          findTokenDelegations(username),
        ]);

        //   
        const symbolArray = resultBalances.map(e => e.symbol);
        const resultMarket = await findMarketMetrics(symbolArray);

        //  
        const calculatedBalances = resultBalances.map(e => {
          //  
          const delegationInfos = resultDelegations.filter(
            ({ symbol }) => symbol === e.symbol,
          );
          const market = resultMarket.find(e3 => e3.symbol === e.symbol) || {};
          const lastPrice = new BigNumber(market.lastPrice || 0);
          const priceChangePercent = market.priceChangePercent || '';
          const priceChangeSteem = market.priceChangeSteem || '';
          const volume = new BigNumber(market.volume || 0);
          const stake = new BigNumber(e.stake || 0);
          const delegationsIn = new BigNumber(e.delegationsIn || 0);
          const delegationsOut = new BigNumber(e.delegationsOut || 0);
          const totalDelegations = delegationsIn.minus(delegationsOut);
          const balance = new BigNumber(e.balance || 0);
          const totalStake = stake.plus(delegationsOut).minus(delegationsOut);
          const balanceStakeSum = balance.plus(totalStake).plus(delegationsOut);
          return {
            lastPrice,
            priceChangePercent,
            priceChangeSteem,
            priceChange: parseFloat(priceChangePercent),
            volume,
            balance,
            balancePrice: balance.times(lastPrice),
            delegationInfos,
            delegationsIn: delegationsIn.toString(),
            delegationsOut: delegationsOut.toString(),
            totalDelegations,
            totalStake,
            balanceStakeSum,
            balanceStakeSumPrice: balanceStakeSum.times(lastPrice),
            pendingUndelegations: new BigNumber(e.pendingUndelegations || 0),
            pendingUnstake: new BigNumber(e.pendingUnstake || 0),
            stake,
            symbol: e.symbol,
          };
        });

        //   
        const calculatedTotalAssetValue = _.sumBy(calculatedBalances, o =>
          o.balanceStakeSumPrice.toNumber(),
        );

        setTotalAssetValue(calculatedTotalAssetValue);
        setBalances(calculatedBalances);
        setLoading(false);
      })();
    }
  }, [username]);

  // 
  const sortedBalances = React.useMemo(() => {
    return balances
      .sort((a, b) => {
        if (sortOrder === 'desc')
          return new BigNumber(b[orderBy]).minus(a[orderBy]).toNumber();
        return new BigNumber(a[orderBy]).minus(b[orderBy]).toNumber();
      })
      .filter(e => {
        if (isChecked) {
          return e.balanceStakeSum.toNumber() > 0;
        }
        return true;
      })
      .filter(e => {
        if (search) {
          return e.symbol.indexOf(search.toUpperCase()) !== -1;
        }
        return true;
      });
  }, [balances, orderBy, sortOrder, isChecked, search]);

  //  
  const toogleSortHandeler = val => {
    if (val === orderBy) {
      setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
    } else {
      setOrderBy(val);
      setSortOrder('desc');
    }
  };

  //   
  const showDialog = e => {
    setOpenDialog(true);
    setDialogItem(e);
  };

  return (
    <React.Fragment>
      <Container maxWidth="lg">
        <Paper>
          <Toolbar className={classes.head}>
            <div>
                :{' '}
              <span className="font_arial">
                {new BigNumber(totalAssetValue).toFormat(3)}
              </span>{' '}
              <span className={classes.upper}>STEEM</span>
              <span className={clsx(classes.upper, classes.tickerTransform)}>
                 $
                {new BigNumber(totalAssetValue).times(steemPrice).toFormat(3)}{' '}
                USD
              </span>
            </div>
            <div className={classes.spacer} />
            <div>@{username}</div>
          </Toolbar>
          <Toolbar className={classes.headOption}>
            <HeaderCheckBox
              onChange={() => setIsChecked(!isChecked)}
              value={isChecked}
            />
            <div className={classes.spacer} />
            <HeaderSerchBox value={search} onChange={v => setSearch(v)} />
          </Toolbar>
          <Divider />
          <div>
            <Table>
              <colgroup>
                <col width="10" />
                <col width="120" />
                <col width="130" />
                <col />
                <col />
                <col />
                <col />
              </colgroup>
              <TableHead>
                <TableRow>
                  <TableCell align="center">Symbol</TableCell>
                  <TableCell>
                    <TableSortLabel
                      active={orderBy === 'priceChange'}
                      direction={sortOrder}
                      onClick={() => toogleSortHandeler('priceChange')}
                    >
                      (24H)
                    </TableSortLabel>
                  </TableCell>
                  <TableCell align="right">
                    <TableSortLabel
                      active={orderBy === 'lastPrice'}
                      direction={sortOrder}
                      onClick={() => toogleSortHandeler('lastPrice')}
                    >
                      <span className={clsx(classes.legal, classes.upper)}>
                        (STEEM)
                      </span>
                      Price
                    </TableSortLabel>
                  </TableCell>
                  {/* <TableCell>Token Name</TableCell> */}
                  <TableCell align="right">
                    <TableSortLabel
                      active={orderBy === 'balanceStakeSumPrice'}
                      direction={sortOrder}
                      onClick={() => toogleSortHandeler('balanceStakeSumPrice')}
                    >
                      Stake+Balance
                    </TableSortLabel>
                  </TableCell>

                  <TableCell align="right">
                    <TableSortLabel
                      active={orderBy === 'balancePrice'}
                      direction={sortOrder}
                      onClick={() => toogleSortHandeler('balancePrice')}
                    >
                      Balance
                    </TableSortLabel>
                  </TableCell>
                  <TableCell align="right">
                    <TableSortLabel
                      active={orderBy === 'stake'}
                      direction={sortOrder}
                      onClick={() => toogleSortHandeler('stake')}
                    >
                      Stake
                    </TableSortLabel>
                  </TableCell>
                  <TableCell align="right">
                    <TableSortLabel
                      active={orderBy === 'totalDelegations'}
                      direction={sortOrder}
                      onClick={() => toogleSortHandeler('totalDelegations')}
                    >
                      Delegations
                    </TableSortLabel>
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {sortedBalances.map(e => (
                  <TableRow
                    key={e.symbol}
                    hover
                    onClick={() => {}}
                    style={{ cursor: 'pointer' }}
                  >
                    <TableCell component="th" scope="row" align="center">
                      <strong>{e.symbol}</strong>
                    </TableCell>
                    <TableCell>{e.priceChangePercent}</TableCell>
                    <TableCell align="right">
                      {e.lastPrice.toFixed(5)}
                      <span className={clsx(classes.legal, classes.upper)}>
                        &nbsp; ${e.lastPrice.times(steemPrice).toFormat(3)} usd
                      </span>
                    </TableCell>
                    <TableCell align="right">
                      {e.balanceStakeSum.toFormat(5)}
                      <span className={clsx(classes.legal, classes.upper)}>
                         {e.balanceStakeSumPrice.toFormat(3)} STEEM
                      </span>
                      <span className={clsx(classes.legal, classes.upper)}>
                         $
                        {e.balanceStakeSumPrice.times(steemPrice).toFormat(3)}{' '}
                        usd
                      </span>
                    </TableCell>
                    <TableCell align="right">
                      {e.balance.toFormat(5)}
                      <span className={clsx(classes.legal, classes.upper)}>
                         {e.balancePrice.toFormat(3)} STEEM
                      </span>
                      <span className={clsx(classes.legal, classes.upper)}>
                         ${e.balancePrice.times(steemPrice).toFormat(3)} usd
                      </span>
                    </TableCell>
                    <TableCell align="right">{e.stake.toFormat(3)}</TableCell>
                    <TableCell align="right">
                      {e.totalDelegations.toString()}
                      {e.delegationInfos.length ? (
                        <IconButton size="small" onClick={() => showDialog(e)}>
                          <InfoIcon />
                        </IconButton>
                      ) : null}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
            {loading && <LinearProgress />}
          </div>
        </Paper>
      </Container>
      <Dialog
        fullWidth
        maxWidth="md"
        onClose={() => setOpenDialog(false)}
        aria-labelledby="simple-dialog-title"
        open={openDialog}
      >
        <DialogTitle id="simple-dialog-title" dividers>
          Delegations
        </DialogTitle>
        {dialogItem &&
        (parseFloat(dialogItem.delegationsIn) ||
          parseFloat(dialogItem.delegationsOut)) ? (
          <DialogContent dividers style={{ overflowY: 'initial' }}>
            <DialogContentText>
              <Box m={1} style={{ textAlign: 'right' }}>
                <p>
                  Total Received: <strong>{dialogItem.delegationsIn}</strong>{' '}
                  <span style={{ color: '#9194a4', fontSize: 12 }}>
                    {dialogItem.symbol}
                  </span>
                </p>
                <p>
                  Total Delegated:{' '}
                  <strong>
                    {parseFloat(dialogItem.delegationsOut)
                      ? `-${dialogItem.delegationsOut}`
                      : 0}
                  </strong>{' '}
                  <span style={{ color: '#9194a4', fontSize: 12 }}>
                    {dialogItem.symbol}
                  </span>
                </p>
              </Box>
            </DialogContentText>
          </DialogContent>
        ) : null}
        <Table>
          <TableHead>
            <TableRow>
              <TableCell align="center">Type</TableCell>
              <TableCell align="center">Symbol</TableCell>
              <TableCell>Account</TableCell>
              <TableCell align="right">Amount</TableCell>
              <TableCell align="center">Created</TableCell>
              <TableCell align="center">Updated</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {dialogItem
              ? dialogItem.delegationInfos.map((e, i) => {
                  const type = e.from === username ? 'Delegated' : 'Received';
                  const account = e.from === username ? e.to : e.from;
                  const quantity = new BigNumber(e.quantity);
                  const quantityPrice = quantity.times(dialogItem.lastPrice);
                  return (
                    <TableRow
                      key={String(i)}
                      hover
                      onClick={() => {}}
                      style={{ cursor: 'pointer' }}
                    >
                      <TableCell align="center">{type}</TableCell>
                      <TableCell align="center">{e.symbol}</TableCell>
                      <TableCell>{account}</TableCell>
                      <TableCell align="right">
                        {type === 'Delegated' ? '-' : ''}
                        {quantity.toString()}{' '}
                        <span style={{ color: '#9194a4', fontSize: 12 }}>
                          {dialogItem.symbol}
                        </span>
                        <span className={clsx(classes.legal, classes.upper)}>
                           {quantityPrice.toFormat(3)} STEEM
                        </span>
                        <span className={clsx(classes.legal, classes.upper)}>
                           ${quantityPrice.times(steemPrice).toFormat(3)} usd
                        </span>
                      </TableCell>
                      <TableCell align="center">
                        {moment(e.created).format('YYYY-MM-DD')}
                        <br />
                        {moment(e.created).format('HH:mm:ss')}
                      </TableCell>
                      <TableCell align="center">
                        {moment(e.updated).format('YYYY-MM-DD')}
                        <br />
                        {moment(e.updated).format('HH:mm:ss')}
                      </TableCell>
                    </TableRow>
                  );
                })
              : null}
          </TableBody>
        </Table>
      </Dialog>
    </React.Fragment>
  );
};

Wallet.propTypes = {
  match: PropTypes.shape({
    params: PropTypes.shape({
      username: PropTypes.string,
    }),
  }).isRequired,
  context: PropTypes.shape({
    steemPrice: PropTypes.number,
    loginAccount: PropTypes.string,
  }).isRequired,
};

export default withConsumer(Wallet);
