// const CandleChart = ({ title, symbol }) => {
class AreaChart extends React.PureComponent {

  static propTypes = {
    loading: PropTypes.bool.isRequired,
    symbol: PropTypes.string.isRequired,
  }

  constructor(props) {
    super(props);
    console.log('props', props);

    this.chartRef = React.createRef();

    this.state = {
      chartOptions,
    };
  }

  componentWillReceiveProps(nextProps) {
    // console.log('componentWillReceiveProps1', this.props.symbol);
    console.log('componentWillReceiveProps2', nextProps)
    // if (nextProps.symbol && this.props.symbol !== nextProps.symbol) {
      // console.log('componentWillReceiveProps');
      this.updateChart(nextProps);
    // }

    
  }

  // componentDidMount() {
  //   this.updateChart();
  // }

  updateChart(props) {
    const { loading, symbol, buyBook, sellBook } = props;
    if(loading) {
      return this.chartRef.current.chart.showLoading();
    } else {
    // this.chartRef.current.chart.showLoading();

    // const { buyBook, sellBook, symbol } = this.props;

    // this.setState({
    //   chartOptions: {
    //     title: {
    //       text: `${symbol} Market Depth`,
    //     },
    //   },
    // });

    // const opts = {
    //   query: { symbol  },
    // };
    // Promise.all([
    //   findBuyBook(opts), //  
    //   findSellBook(opts), //  
    // ]).then(([buyBook, sellBook]) => {
    //   console.log({ buyBook, sellBook });
    // prepare chart datasets
    const buyOrderLabels = _.uniq(buyBook.map(o => parseFloat(o.price))).sort(
      (a, b) => b - a,
    );
    let buyOrderDataset = [];
    let buyOrderCurrentVolume = 0;
    buyOrderLabels.forEach(label => {
      const matchingBuyOrders = buyBook.filter(
        o => parseFloat(o.price) === label,
      );

      if (matchingBuyOrders.length === 0) {
        buyOrderDataset.push(null);
      } else {
        buyOrderCurrentVolume += matchingBuyOrders.reduce(
          (acc, val) => acc + parseFloat(val.quantity),
          0,
        );
        buyOrderDataset.push([label, buyOrderCurrentVolume]);
      }
    });
    // buyOrderDataset = buyOrderDataset.sort(([a], [b]) => a - b);
    buyOrderDataset = buyOrderDataset.filter(([price]) => price >= buyOrderDataset[0][0] / 5)
    buyOrderDataset = buyOrderDataset.reverse();

    const sellOrderLabels = _.uniq(sellBook.map(o => parseFloat(o.price))).sort(
      (a, b) => a - b,
    );
    let sellOrderDataset = []; // _.fill(Array(buyOrderDataset.length), null);
    let sellOrderCurrentVolume = 0;
    sellOrderLabels.forEach(label => {
      const matchingSellOrders = sellBook.filter(
        o => parseFloat(o.price) === label,
      );
      if (matchingSellOrders.length === 0) {
        sellOrderDataset.push(null);
      } else {
        sellOrderCurrentVolume += matchingSellOrders.reduce(
          (acc, val) => acc + parseFloat(val.quantity),
          0,
        );
        sellOrderDataset.push([label, sellOrderCurrentVolume]);
      }
    });
    sellOrderDataset = sellOrderDataset.filter(([price]) => price <= sellOrderDataset[0][0] * 5)
   
    // sellOrderDataset.reverse();
    console.log({ buyOrderDataset, sellOrderDataset });

    this.setState({
      chartOptions: {
        title: {
          text: `${symbol} Market Depth`,
        },
        series: [
          {
            name: 'Buy',
            data: buyOrderDataset,
            color: '#03a7a8',
            fillOpacity: 0.3,
          },
          {
            name: 'Sell',
            data: sellOrderDataset,
            color: '#fc5857',
            fillOpacity: 0.3,
          },
        ],
      },
    });
    this.chartRef.current.chart.hideLoading();
    // });
  }
  }

  render() {
    console.log('loading', this.props.loading);
    return (
      <div>
      {this.props.loading && <LinearProgress />}
        <HighchartsReact
          highcharts={Highcharts}
          options={this.state.chartOptions}
          ref={this.chartRef}
          />
      </div>
    );
  }
}

export default AreaChart;
