//     
export const findMarketMetrics = (
  symbols,
  limit = 1000,
  offset = 0,
  opts = [],
) => {
  // const whereORQuery = symbols.map(symbol => ({
  //   symbol,
  // }));
  // { symbol: { $in: [<value1>, <value2>, ... <valueN> ] }
  // return ssc.find('market', 'metrics', { $or: whereORQuery }, 1000, 0, []);
  let query = {};
  if (Array.isArray(symbols)) {
    query = { symbol: { $in: symbols } };
  } else if (typeof symbols === 'string') {
    query = { symbol: symbols };
  } else {
    query = symbols;
  }
  // console.log('findMarketMetrics', 'query', query);
  return ssc.find('market', 'metrics', query, limit, offset, opts);
};

//   
export const findTokenBalance = (
  query = { symbol: 'ZZAN' },
  limit = 1000,
  offset = 0,
  indexes = [],
) => {
  return new Promise((resolve, reject) => {
    ssc.find(
      'tokens',
      'balances',
      query,
      limit,
      offset,
      indexes,
      (err, result) => {
        if (err) return reject(err);
        resolve(result);
      },
    );
  });
};

//    
export const findAllTokenBalance = async (symbol = 'ZZAN') => {
  const query = { symbol };
  let result = [];
  const limit = 1000;
  for (let offset = 0; ; offset += limit) {
    const balances = await findTokenBalance(query, limit, offset);
    result = result.concat(balances);
    if (balances.length < limit) break;
  }
  return result;
};

