// import InputBase from '@material-ui/core/InputBase';
import SearchIcon from '@material-ui/icons/Search';

import LinearProgress from '@material-ui/core/LinearProgress';
// import CircularProgress from '@material-ui/core/CircularProgress';
import SwapHorizontalCircle from '@material-ui/icons/SwapHorizontalCircleOutlined';

import BigNumber from 'bignumber.js';
import {
  // getPrice,
  findTokens,
  findMarketMetrics,
} from '../../../service/SteemEngineService';

const DEFAULT_ICON_URL =
  'https://steemitimages.com/p/UF7UDccXmKiVHGe6ggQML6oJhGERjK1WsQL9WtTC39Wp1Kx4qyisLxa77jjN4razEV21dXUxFtkerNMuK8TJLorYNstgmxxLW1pdsaAj2QNaPALz8UgsdNR6VyJ1HYENzkFDECNe2nEduKNMDsEea6VFGg83paXK3Fojb7AcGr7gCjbNUQQfaqU9cvUFPW6LjGKaSB8kqFZqcLEKwaaiWiB3oDKx1tQ3t1dhAPCjdGZBpBhcUpDequukTopzU2c5LpT18L7yJ4ikf?format=match&mode=fit&width=16&height=16';

const useStyles = makeStyles(theme => ({
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    marginRight: 10,
    width: '100%',
  },
  searchIcon: {
    width: theme.spacing(7),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 7),
  },
  icon: {
    width: 16,
    height: 16,
    borderRadius: 8,
    marginTop: 5,
    backgroundColor: '#eee',
  },
}));

const getSmallImage = url => {
  if (!url || !url.startsWith('http')) {
    return DEFAULT_ICON_URL;
  }
  if (/^https?:\/\/(.*)\.(jpe?g|gif|png)$/i.test(url)) {
    return `https://steemitimages.com/32x32/${url}`;
  }
  return url;
};

const CoinSwitch = ({
  match,
  history,
  steemPrice,
  selectToken,
  setSelectToken,
  setTokenInfo,
}) => {
  const [loading, setLoading] = React.useState(true);
  const [tokens, setTokens] = React.useState([]);
  const [orderby, setOrderby] = React.useState('volume');
  const [sortOrder, setSortOrder] = React.useState('desc');
  const [switchPrice, setSwitchPrice] = React.useState(false);
  const [search, setSearch] = React.useState('');

  React.useEffect(() => {
    // https://postpromoter.net/api/prices
    Promise.all([findTokens({}), findMarketMetrics({})]).then(
      ([_tokens, _metric]) => {
        // console.log({ _tokens, _metric });
        const resultTokens = _metric.map(e => {
          const {
            issuer,
            precision,
            name,
            stakingEnabled,
            supply,
            totalStaked,
            undelegationCooldown,
            unstakingCooldown,
            circulatingSupply,
            delegationEnabled,
            maxSupply,
            metadata,
          } = _tokens.find(({ symbol }) => symbol === e.symbol);
          const { icon } = JSON.parse(metadata);
          return {
            symbol: e.symbol,
            highestBid: parseFloat(e.highestBid),
            lastDayPrice: parseFloat(e.lastDayPrice),
            // lastDayPriceExpiration: e.lastDayPriceExpiration,
            lastPrice: parseFloat(e.lastPrice), // 
            lowestAsk: parseFloat(e.lowestAsk),
            priceChangePercent: e.priceChangePercent, // 
            priceChangeSteem: parseFloat(e.priceChangeSteem),
            volume: parseFloat(e.volume),
            // volumeExpiration: e.symbol,
            icon,
            issuer,
            precision,
            name,
            stakingEnabled,
            supply,
            totalStaked,
            undelegationCooldown,
            unstakingCooldown,
            circulatingSupply,
            delegationEnabled,
            maxSupply,
          };
        });
        // .filter(e => {
        //   if (e.volume > 10) return true;
        //   return false;
        // });
        // .sort((a, b) => b.volume - a.volume);
        // .sort((a, b) => b.lastPrice - a.lastPrice);
        setTokens(resultTokens);
        setLoading(false);

        // if (!selectToken) {
        //   setSelectToken(resultTokens[0].symbol);
        //   setTokenInfo(resultTokens[0]);
        // }
      },
    );
  }, []);

  const orderdTokens = React.useMemo(() => {
    return tokens
      .sort((a, b) => {
        if (sortOrder === 'desc') return b[orderby] - a[orderby];
        return a[orderby] - b[orderby];
      })
      .filter(e => {
        if (search) {
          return (
            e.symbol.indexOf(search.toUpperCase()) !== -1 ||
            e.name.indexOf(search) !== -1
          );
        }
        return true;
      });
  }, [orderby, sortOrder, tokens, search]);

  // const seletTokenHandler = React.useCallback(() => {
  //   setSelectToken()
  // }, []);

  const toogleSwitchPriceHandler = () => {
    setSwitchPrice(!switchPrice);
  };

  const classes = useStyles();
  // console.log('tokens', tokens);
  return (
    <div id="coin-switch" className={classNames('mod', 'coin-switch')}>
      <div className={classNames('mod-head', 'tabs', 'coin-switch-tabs')}>
        <div className="quotes">
          {/* <span className="mock-a fav">
            <em>Fav</em>
          </span> */}
          <span className={classNames('mock-a', 'upper', { cur: true })}>
            steem
          </span>
          <span className={classNames('mock-a', 'upper', { cur: false })}>
            sbd
          </span>
        </div>
        <div className="actions">
          <div className="input-box">
            <input
              placeholder=""
              maxLength="9"
              type="text"
              value={search}
              onChange={e => setSearch(e.target.value)}
            />
            {/* <i className="hb_icon_search" /> */}
            <SearchIcon
              className="hb_icon_search"
              style={{
                fontSize: 16,
                color: '#61688a',
                left: 5,
                position: 'absolute',
                left: 8,
                top: `calc(50% - 6px)`,
              }}
            />
            <i className="hb_icon_clear" style={{ display: 'none' }} />
          </div>
          <button
            type="button"
            className={classNames('upper', 'button', { active: switchPrice })}
            onClick={toogleSwitchPriceHandler}
          >
            {/* <i className="hb_icon_switch" /> */}
            <SwapHorizontalCircle style={{ fontSize: 16, marginRight: 4 }} />
            usd
          </button>
          {/* <div className={classes.search}>
          <div className={classes.searchIcon}>
            <SearchIcon />
          </div>
          <InputBase
            placeholder=""
            type="search"
            margin="dense"
            classes={{
              root: classes.inputRoot,
              input: classes.inputInput,
            }}
            inputProps={{ 'aria-label': 'Search' }}
          />
        </div> */}
        </div>
      </div>
      <div className="coin-switch-content">
        <dl className="usdt">
          <dt>
            <span className="currency">
               <i className={orderby === 'symbol' ? sortOrder : {}} />
            </span>
            <span className="price">
               {switchPrice && <em className="upper">(usd)</em>}
              <i className={orderby === 'lastPrice' ? sortOrder : {}} />
            </span>
            <span className="rate">
              {' '}
              <i
                className={orderby === 'priceChangePercent' ? sortOrder : {}}
              />
            </span>
            <span className="amount24">
              24h Volume <i className={orderby === 'volume' ? sortOrder : {}} />
            </span>
          </dt>
          <dd>
            <dl>
              <dt>{loading && <LinearProgress />}</dt>
              {orderdTokens.map(token => {
                return (
                  <dd
                    key={token.symbol}
                    className={classNames('cs-list', 'upper', 'online', {
                      cur: token.symbol === selectToken,
                    })}
                  >
                    <i className={classNames('hb_icon_marked', 'unmarked')}>
                      <img
                        onError={e => {
                          e.target.src = DEFAULT_ICON_URL;
                        }}
                        src={getSmallImage(token.icon)}
                        className={classes.icon}
                        alt={token.symbol}
                      />
                    </i>
                    <a
                      // href={`${match.url}${token.symbol}_steem`}
                      onClick={() => {
                        // console.log('setSelectToken', token.symbol);
                        setSelectToken(token.symbol);
                        setTokenInfo(token);
                        history.push(
                          `${match.url}/${token.symbol.toLowerCase()}_steem`,
                        );
                      }}
                    >
                      <span className="currency">
                        {token.symbol} <em className="tags" />
                      </span>
                      <span className="price" growing-ignore="true">
                        {switchPrice
                          ? new BigNumber(token.lastPrice)
                              .times(steemPrice)
                              .toFixed(6)
                          : token.lastPrice}
                      </span>
                      <span
                        growing-ignore="true"
                        className={classNames(
                          'rate',
                          token.priceChangePercent.startsWith('-')
                            ? 'color-down'
                            : 'color-up',
                        )}
                      >
                        {token.priceChangePercent}
                      </span>
                      <span className="amount"> {token.volume}</span>
                    </a>
                  </dd>
                );
              })}
              {/* <dd className="loading"><CircularProgress color="#ddd" size={30} /></dd> */}
            </dl>
          </dd>
        </dl>
      </div>
    </div>
  );
};
CoinSwitch.propTypes = {
  match: PropTypes.shape().isRequired,
  history: PropTypes.shape().isRequired,
  steemPrice: PropTypes.number.isRequired,
  selectToken: PropTypes.string.isRequired,
  setSelectToken: PropTypes.func.isRequired,
  setTokenInfo: PropTypes.func.isRequired,
};

export default CoinSwitch;
