import React from 'react';
import {
  Redirect,
  HashRouter as Router,
  Route,
  Link as RouteLink,
} from 'react-router-dom';

import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import Button from '@material-ui/core/Button';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';
import MenuIcon from '@material-ui/icons/Menu';
import NotificationsIcon from '@material-ui/icons/Notifications';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import AccountCircle from '@material-ui/icons/AccountCircle';
import ArrowForward from '@material-ui/icons/KeyboardArrowRight';

import Avatar from '@material-ui/core/Avatar';

import Orderbook from './page/Orderbook';
import Exchange from './page/Exchange';
import Market from './page/Market';
import TokenInfo from './page/TokenInfo';
import Wallet from './page/Wallet';

import LoginDialog from './components/LoginDialog';

import { Provider } from './Context';
import { getPrice } from './service/SteemEngineService';

import withTracker from './withTracker';

const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
    backgroundColor: '#2e1534',
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  menuButtonHidden: {
    display: 'none',
  },
  title: {
    flexGrow: 1,
  },
  drawerPaper: {
    position: 'relative',
    whiteSpace: 'nowrap',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9),
    },
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto',
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  appBarButton: {
    height: '100%',
  },
  spacer: {
    flex: '1 1 100%',
  },
}));

export default function Dashboard({ match, history }) {
  const classes = useStyles();

  const [openLoginDialog, setOpenLoginDialog] = React.useState(false);
  const [selectedTab, setSelectedTab] = React.useState();

  const [steemPrice, setSteemPrice] = React.useState(0);
  const [sbdPrice, setSbdPrice] = React.useState(0);

  const [username, setUsername] = React.useState('');

  React.useEffect(() => {
    // console.log(history)
    const { pathname } = history.location;
    // if (pathname !== "/")
    setSelectedTab(pathname);

    getPrice().then(_price => {
      setSteemPrice(_price.steem_price);
      setSbdPrice(_price.sbd_price);
    }); //

    setUsername(window.sessionStorage.getItem('username'));
  }, []);

  const loginHandler = React.useCallback(() => {
    if (window.steem_keychain) {
      setOpenLoginDialog(true);
    } else {
      window.open(
        'https://chrome.google.com/webstore/detail/steem-keychain/lkcjlnjfpbikmcmbachjpdbijejflpcm?hl=ko',
      );
    }
  });

  const onLoginHandler = username => {
    window.steem_keychain.requestHandshake(() => {
      // console.log('Handshake received!');
      window.steem_keychain.requestSignBuffer(
        username,
        'signin',
        'Posting',
        response => {
          console.log(response);
          const { success, result, message, error } = response;
          if (success) {
            setOpenLoginDialog(false);
            window.sessionStorage.setItem('username', username);
            setUsername(username);
          }
        },
      );
    });
  };

  return (
    <React.Fragment>
      <div className={classes.root}>
        <CssBaseline />
        <AppBar>
          <Toolbar className={classes.toolbar} variant="dense">
            <Typography
              component="h1"
              variant="h6"
              color="inherit"
              noWrap
              className={classes.title}
            >
              {/* Happigon Exchange */}
              <img
                src={require('./logo.png')}
                height={40}
                alt="happigon"
                style={{ marginBottom: -10 }}
              />
              {/* <Button className={classes.appBarButton} color="inherit">Richlist</Button> */}
            </Typography>
            <Tabs
              value={selectedTab}
              onChange={(event, value) => setSelectedTab(value)}
              aria-label="Simple tabs example"
            >
              {/* <Tab label="Richlist" onClick={() => history.push(`/holders`)} />
            <Tab label="Orderbook" onClick={() => history.push(`/orderbook`)} /> */}

              <Tab
                wrapped
                label="Tokens"
                value="/tokens"
                onClick={() => history.push(`/tokens`)}
              />
              <Tab
                label="Exchange"
                value="/exchange"
                onClick={() => history.push(`/exchange`)}
              />
              {username ? (
                <Tab
                  label="Wallet"
                  value="/wallet"
                  onClick={() => history.push(`/wallet/${username}`)}
                />
              ) : null}
            </Tabs>
            {/* <div className={classes.spacer} /> */}
            {username ? (
              <Avatar
                alt={username}
                src={`https://steemitimages.com/u/${username}/avatar`}
                className={classes.avatar}
              />
            ) : (
              <Button onClick={loginHandler}>Login</Button>
            )}
          </Toolbar>
        </AppBar>
        <main className={classes.content}>
          <div className={classes.appBarSpacer} />
          <Provider
            value={{
              steemPrice,
              sbdPrice,
              loginAccount: username,
            }}
          >
            <Route
              exact
              path="/"
              component={() => <Redirect to="/exchange" />}
            />
            <Route path="/tokens" component={withTracker(Market)} />
            <Route path="/token/:symbol" component={withTracker(TokenInfo)} />
            <Route path="/exchange" component={Exchange} />
            <Route
              path={[`/wallet/:username`, `/wallet`]}
              component={withTracker(Wallet)}
            />
            <Route
              path="/orderbook/:market/:symbol"
              component={withTracker(Orderbook)}
            />
          </Provider>
        </main>
      </div>
      <LoginDialog
        open={openLoginDialog}
        onClose={() => setOpenLoginDialog(false)}
        onLogin={onLoginHandler}
      />
    </React.Fragment>
  );
}
