import React from 'react';
import './Exchange.scss';

// import _ from 'lodash';
import PropTypes from 'prop-types';
// import clsx from "clsx";
import classNames from 'classnames';

// import moment from "moment";
import BigNumber from 'bignumber.js';
import commaNumber from 'comma-number';
// import LinearProgress from '@material-ui/core/LinearProgress';

// import Highcharts from 'highcharts';
// import HighchartsReact from 'highcharts-react-official';
// import { TVChartContainer } from '../../components/TVChartContainer';

import CoinSwitch from './components/CoinSwitch';
import OrderBook from './components/OrderBook';
import MarketTrades from './components/MarketTrades';

import { withConsumer } from '../../Context';

// import useStyles from './styles';

import {
  // getPrice,
  // findTokens,
  // findMarketMetrics,
  findBuyBook,
  findSellBook,
  findTradesHistory,
} from '../../service/SteemEngineService';

import AreaChart from './components/AreaChart';

const Exchange = ({ match, history, context }) => {
  // console.log('props', props);
  // const classes = useStyles();

  const [sellBook, setSellBook] = React.useState([]);
  const [buyBook, setBuyBook] = React.useState([]);
  const [tradesHistory, setTradesHistory] = React.useState([]);
  const [selectToken, setSelectToken] = React.useState('');

  const [tokenInfo, setTokenInfo] = React.useState();
  const [loading, setLoading] = React.useState(false);
  const [selectTradesTab, setSelectTradesTab] = React.useState(0);

  const { steemPrice } = context;

  React.useEffect(() => {
    if (selectToken) {
      setLoading(true);
      const opts = {
        query: { symbol: selectToken },
      };
      Promise.all([
        findBuyBook(opts), //  
        findSellBook(opts), //  
        findTradesHistory(opts), //  
      ]).then(([resBuyBook, resSellBook, resTradesHistory]) => {
        setBuyBook(resBuyBook);
        setSellBook(resSellBook);
        setTradesHistory(resTradesHistory);
        setLoading(false);
      });
    }
  }, [selectToken]);

  return (
    <React.Fragment>
      <div
        className={classNames('page-global-exchange', 'page')}
        style={{ marginTop: -15 }}
      >
        <div className="content-top">
          <div className="left">
            <CoinSwitch
              match={match}
              history={history}
              steemPrice={steemPrice}
              selectToken={selectToken}
              setSelectToken={setSelectToken}
              setTokenInfo={setTokenInfo}
            />
          </div>
          <div className="right">
            <div className="l">
              <div className={classNames('mod', 'trading-chart')}>
                <div className="mod-head">
                  {selectToken && (
                    <div className="ticker">
                      <span stop="1" className="symbol-name">
                        {selectToken}/STEEM
                      </span>
                      <div growing-ignore="true" className="price-container">
                        <span className="price color-up">
                          {tokenInfo.lastPrice}
                        </span>
                        <span className="estimate">
                          {' '}
                          {new BigNumber(tokenInfo.lastPrice)
                            .times(steemPrice)
                            .toFixed(6)}{' '}
                          USD
                        </span>
                      </div>
                      <dl growing-ignore="true" className="change">
                        <dt></dt>
                        <dd
                          className={`color-${
                            tokenInfo.priceChangePercent.startsWith('-')
                              ? 'down'
                              : 'up'
                          }`}
                        >
                          {tokenInfo.priceChangePercent}
                        </dd>
                      </dl>
                      <dl growing-ignore="true" className="high">
                        <dt>24H Ask</dt>
                        <dd>{tokenInfo.lowestAsk}</dd>
                      </dl>
                      <dl growing-ignore="true" className="low">
                        <dt>24H Bid</dt>
                        <dd>{tokenInfo.highestBid}</dd>
                      </dl>
                      <dl growing-ignore="true" className="amount">
                        <dt>24H </dt>
                        <dd>
                          {tokenInfo.volume.toFixed(2)} STEEM / $
                          {commaNumber(
                            new BigNumber(tokenInfo.volume)
                              .times(steemPrice)
                              .toFixed(2),
                          )}
                        </dd>
                      </dl>
                    </div>
                  )}
                </div>
                <div className={classNames('mod', 'mod-body')}>
                  {/* {selectToken && <TVChartContainer />} */}
                  {selectToken && (
                    <AreaChart
                      title={`${selectToken}/STEEM`}
                      symbol={selectToken}
                      sellBook={sellBook}
                      buyBook={buyBook}
                      loading={loading}
                      // tradesHistory={tradesHistory}
                    />
                  )}
                </div>
              </div>
            </div>
            <div className={classNames('r', 'trades-container')}>
              <div className="global-trades">
                <ul>
                  <li className={classNames({ cur: selectTradesTab === 0 })}>
                    <span
                      role="button"
                      tabIndex={0}
                      onClick={() => setSelectTradesTab(0)}
                      onKeyPress={() => setSelectTradesTab(0)}
                    >
                      
                    </span>
                  </li>
                  <li className={classNames({ cur: selectTradesTab === 1 })}>
                    <span
                      role="button"
                      tabIndex={0}
                      onClick={() => setSelectTradesTab(1)}
                      onKeyPress={() => setSelectTradesTab(1)}
                    >
                      
                    </span>
                  </li>
                </ul>
                <div>
                  <OrderBook
                    sellBook={sellBook}
                    symbol={selectToken}
                    steemPrice={steemPrice}
                    buyBook={buyBook}
                    loading={loading}
                    selectedTab={selectTradesTab === 0}
                  />
                  <MarketTrades
                    steemPrice={steemPrice}
                    symbol={selectToken}
                    loading={loading}
                    tradesHistory={tradesHistory}
                    selectedTab={selectTradesTab === 1}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

Exchange.propTypes = {
  history: PropTypes.shape().isRequired,
  match: PropTypes.shape().isRequired,
  context: PropTypes.shape({
    steemPrice: PropTypes.number,
    sbdPrice: PropTypes.number,
  }).isRequired,
};

// https://apexcharts.com/react-chart-demos/candlestick-charts/combo/
// https://www.highcharts.com/stock/demo/candlestick
export default withConsumer(Exchange);
