import {
  findTokens,
  findMarketMetrics,
  getScotConfig,
} from '../../service/SteemEngineService';

const DEFAULT_ICON_URL =
  'https://steemitimages.com/p/UF7UDccXmKiVHGe6ggQML6oJhGERjK1WsQL9WtTC39Wp1Kx4qyisLxa77jjN4razEV21dXUxFtkerNMuK8TJLorYNstgmxxLW1pdsaAj2QNaPALz8UgsdNR6VyJ1HYENzkFDECNe2nEduKNMDsEea6VFGg83paXK3Fojb7AcGr7gCjbNUQQfaqU9cvUFPW6LjGKaSB8kqFZqcLEKwaaiWiB3oDKx1tQ3t1dhAPCjdGZBpBhcUpDequukTopzU2c5LpT18L7yJ4ikf?format=match&mode=fit&width=16&height=16';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing(3),
    overflowX: 'auto',
  },
  table: {
    minWidth: 650,
  },
  icon: {
    width: 20,
    height: 20,
    borderRadius: 10,
    // marginTop: 5,
    backgroundColor: '#eee',
    marginRight: 8,
    verticalAlign: 'middle',
  },
  colorUp: {
    color: '#12b886',
  },
  colorDown: {
    color: '#fa5252',
  },
  activeButton: {
    borderColor: '#303f9f',
  },
  searchRoot: {
    margin: theme.spacing(1),
    flexGrow: 1,
    marginLeft: 'calc(100% - 35%)',
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(1),
      width: 'auto',
    },
  },
  searchIcon: {
    width: theme.spacing(7),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 7),
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: 120,
      '&:focus': {
        width: 200,
      },
    },
  },
}));

const StyledTableCell = withStyles(theme => ({
  head: {
    backgroundColor: '#131636', // theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const Market = ({ context, history, location, match }) => {
  // console.log('Market', { history, location, match });
  const classes = useStyles();

  const [loading, setLoading] = React.useState(true);
  const [tokens, setTokens] = React.useState([]);
  const [orderby, setOrderby] = React.useState('volume');
  const [sortOrder, setSortOrder] = React.useState('desc');
  const [switchPrice, setSwitchPrice] = React.useState(false);
  const [search, setSearch] = React.useState('');
  const [selectTab, setSelectTab] = React.useState('0');

  React.useEffect(() => {
    if (history.action === 'POP') {
      if (window.history.state) {
        setTokens(window.history.state);
        setLoading(false);
        return;
      }
    }
    // https://postpromoter.net/api/prices
    Promise.all([findTokens({}), findMarketMetrics({}), getScotConfig()]).then(
      ([_tokens, _metric, _config]) => {
        // console.log({ _tokens, _metric, _config });
        const resultTokens = _metric.map(e => {
          const {
            issuer,
            precision,
            name,
            stakingEnabled,
            supply,
            totalStaked,
            undelegationCooldown,
            unstakingCooldown,
            circulatingSupply,
            delegationEnabled,
            maxSupply,
            metadata,
          } = _tokens.find(({ symbol }) => symbol === e.symbol);
          const isScot = _config.findIndex(({ token }) => token === e.symbol);
          let scotMetadataValue = '';
          if (isScot !== -1) {
            const {
              // json_metadata_key: ,
              json_metadata_value: jsonMetadataValue,
            } = _config[isScot];
            scotMetadataValue = jsonMetadataValue;
          }
          const marketCap = new BigNumber(circulatingSupply)
            // .plus(totalStaked)
            .times(e.lastPrice)
            .toNumber();
          const { icon } = JSON.parse(metadata);
          return {
            symbol: e.symbol,
            highestBid: parseFloat(e.highestBid),
            lastDayPrice: parseFloat(e.lastDayPrice),
            // lastDayPriceExpiration: e.lastDayPriceExpiration,
            lastPrice: parseFloat(e.lastPrice), // 
            lowestAsk: parseFloat(e.lowestAsk),
            priceChangePercent: e.priceChangePercent, // 
            priceChangeSteem: parseFloat(e.priceChangeSteem),
            volume: parseFloat(e.volume),
            // volumeExpiration: e.symbol,
            icon,
            issuer,
            precision,
            name,
            stakingEnabled,
            supply,
            totalStaked,
            undelegationCooldown,
            unstakingCooldown,
            circulatingSupply,
            delegationEnabled,
            maxSupply,
            marketCap,
            isScot: isScot !== -1,
            scotMetadataValue,
          };
        });
        // .filter(e => {
        //   if (e.volume > 10) return true;
        //   return false;
        // });
        // .sort((a, b) => b.volume - a.volume);
        // .sort((a, b) => b.lastPrice - a.lastPrice);
        // console.log(resultTokens);
        setTokens(resultTokens);
        setLoading(false);
        window.history.replaceState(resultTokens, '', `#${location.pathname}`);
        // history.replace(location.pathname, resultTokens);
      },
    );
  }, []);

  const orderdTokens = React.useMemo(() => {
    return tokens
      .sort((a, b) => {
        if (sortOrder === 'desc') return b[orderby] - a[orderby];
        return a[orderby] - b[orderby];
      })
      .filter(e => {
        if (search) {
          return (
            e.symbol.indexOf(search.toUpperCase()) !== -1 ||
            e.name.indexOf(search) !== -1
          );
        }
        if (selectTab === '1') {
          return e.isScot;
        }
        return true;
      });
  }, [orderby, sortOrder, tokens, search, selectTab]);

  // const searchTokens = React.useMemo(() => {
  // 	return tokens;
  // }, [tokens, selectTab]);

  const toogleSwitchPriceHandler = () => {
    setSwitchPrice(!switchPrice);
  };

  const toogleSortHandeler = val => {
    if (val === orderby) {
      setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
    } else {
      setOrderby(val);
      setSortOrder('desc');
    }
  };

  return (
    <Container className={classes.container}>
      <Paper className={classes.root}>
        <Toolbar style={{ position: 'relative', backgroundColor: '#131636' }}>
          <Tabs
            onChange={(e, val) => {
              // console.log('onChange', val);
              setSelectTab(val);
            }}
            value={selectTab}
            indicatorColor="primary"
            // textColor="primary"
            aria-label="simple tabs example"
          >
            <Tab
              value="0"
              label="All Tokens"
              id="full-width-tab-0"
              aria-controls="full-width-tabpanel-0"
            />
            <Tab
              value="1"
              label="SCOT Tokens"
              id="full-width-tab-1"
              aria-controls="full-width-tabpanel-1"
            />
          </Tabs>
          <ButtonGroup
            size="small"
            // variant="contained"
            // color="primary"
            aria-label="STEEM, USD"
            style={{ position: 'absolute', right: 20 }}
          >
            <Button
              onClick={toogleSwitchPriceHandler}
              className={classNames({
                'MuiButtonGroup-groupedContained': !switchPrice,
                'MuiButtonGroup-groupedContainedPrimary': !switchPrice,
                'MuiButton-contained': !switchPrice,
                'MuiButton-containedPrimary': !switchPrice,
                activeButton: !switchPrice,
              })}
            >
              STEEM
            </Button>
            <Button
              onClick={toogleSwitchPriceHandler}
              className={classNames({
                'MuiButtonGroup-groupedContained': switchPrice,
                'MuiButtonGroup-groupedContainedPrimary': switchPrice,
                'MuiButton-contained': switchPrice,
                'MuiButton-containedPrimary': switchPrice,
                activeButton: switchPrice,
              })}
            >
              USD
            </Button>
            {/* MuiButtonBase-root MuiButton-root MuiButtonGroup-grouped MuiButtonGroup-groupedContained MuiButtonGroup-groupedContainedPrimary MuiButton-outlined MuiButton-contained MuiButton-containedPrimary MuiButton-sizeSmall */}
          </ButtonGroup>
        </Toolbar>
        <div className={classes.searchRoot}>
          <div className={classes.search}>
            <div className={classes.searchIcon}>
              <SearchIcon />
            </div>
            <InputBase
              placeholder=""
              type="search"
              margin="dense"
              value={search}
              onChange={e => setSearch(e.target.value)}
              classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
              }}
              inputProps={{ 'aria-label': 'Search' }}
            />
          </div>
          {/* <Grid container spacing={1} alignItems="flex-end">
            <Grid item>
              <AccountCircle />
            </Grid>
            <Grid item>
              <TextField
                id="input-with-icon-grid"
                variant="outlined"
                label="With a grid"
              />
            </Grid>
          </Grid> */}
        </div>
        <Divider />
        {loading && <LinearProgress />}
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <TableCell width="8">#</TableCell>
              <TableCell>Symbol</TableCell>
              <TableCell>Token Name</TableCell>
              {selectTab === '1' && <TableCell>Scot Tags</TableCell>}
              <TableCell align="right" numeric>
                <TableSortLabel
                  active={orderby === 'marketCap'}
                  direction={sortOrder}
                  onClick={() => toogleSortHandeler('marketCap')}
                >
                  Market Cap
                </TableSortLabel>
              </TableCell>
              <TableCell align="right" numeric>
                <TableSortLabel
                  active={orderby === 'lastPrice'}
                  direction={sortOrder}
                  onClick={() => toogleSortHandeler('lastPrice')}
                >
                  Price {switchPrice && <em className="upper">(usd)</em>}
                </TableSortLabel>
              </TableCell>
              <TableCell align="right" numeric>
                <TableSortLabel
                  active={orderby === 'volume'}
                  direction={sortOrder}
                  onClick={() => toogleSortHandeler('volume')}
                >
                  24H Vol
                </TableSortLabel>
              </TableCell>
              <TableCell align="right">% Chg</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {orderdTokens.map((token, index) => (
              <TableRow
                key={token.symbol}
                hover
                onClick={() => {}}
                style={{ cursor: 'pointer' }}
              >
                <TableCell component="th" scope="row">
                  {index + 1}
                </TableCell>
                <TableCell style={{ whiteSpace: 'nowrap' }}>
                  {token.icon && token.icon.startsWith('http') ? (
                    <img
                      // onError="this.src=' '"
                      onError={e => {
                        /* e.target.onerror = null; */ e.target.src = DEFAULT_ICON_URL;
                      }}
                      src={token.icon}
                      className={classes.icon}
                      alt={token.symbol}
                    />
                  ) : (
                    <img
                      src={DEFAULT_ICON_URL}
                      className={classes.icon}
                      alt={token.symbol}
                    />
                  )}
                  {token.symbol}
                </TableCell>
                <TableCell>{token.name}</TableCell>
                {selectTab === '1' && (
                  <TableCell>
                    {token.scotMetadataValue.split(',').join(', ')}
                  </TableCell>
                )}
                <TableCell align="right">
                  {commaNumber(token.marketCap.toFixed(0))}
                </TableCell>
                <TableCell align="right">
                  {switchPrice
                    ? `$${new BigNumber(token.lastPrice)
                        .times(context.steemPrice)
                        .toFixed(6)}`
                    : token.lastPrice}
                </TableCell>
                <TableCell align="right">
                  {switchPrice
                    ? `$${commaNumber(
                        new BigNumber(token.volume)
                          .times(context.steemPrice)
                          .toFixed(3),
                      )}`
                    : commaNumber(token.volume.toFixed(3))}
                </TableCell>
                <TableCell align="right">
                  <span
                    className={`${
                      token.priceChangePercent.startsWith('-')
                        ? classes.colorDown
                        : classes.colorUp
                    }`}
                  >
                    {token.priceChangePercent}
                  </span>
                </TableCell>
                <TableCell>
                  <Button
                    size="small"
                    color="default"
                    variant="outlined"
                    onClick={() => {
                      history.push(`/token/${token.symbol}`);
                    }}
                  >
                    Detail
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
        {loading && <LinearProgress />}
      </Paper>
    </Container>
  );
};

export default withConsumer(Market);
