import React from 'react';
import _ from 'lodash';
import clsx from 'clsx';
import moment from 'moment';
import classNames from 'classnames';
import {
  fade,
  withStyles,
  makeStyles,
  useTheme,
} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Toolbar from '@material-ui/core/Toolbar';
import InputBase from '@material-ui/core/InputBase';
import SearchIcon from '@material-ui/icons/Search';
import TextField from '@material-ui/core/TextField';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Container from '@material-ui/core/Container';
import LinearProgress from '@material-ui/core/LinearProgress';

import SwipeableViews from 'react-swipeable-views';
import Typography from '@material-ui/core/Typography';
import BigNumber from 'bignumber.js';
import commaNumber from 'comma-number';
import { withConsumer } from '../../../Context';

import { findBuyBook, findSellBook } from '../../../service/SteemEngineService';

const useStyles = makeStyles(theme => ({
  box: {
    // margin: theme.spacing(1),
    padding: theme.spacing(2),
    height: 'auto',
    display: 'block',
    marginBottom: 0,
  },
  root: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1),
  },
  spacer: {
    flex: '1 1 100%',
  },
  actions: {
    color: theme.palette.text.secondary,
  },
  title: {
    flex: '0 0 auto',
  },
  tableWrapper: {
    overflowX: 'auto',
  },
}));

const Orderbook = ({ symbol, context }) => {
  // console.log('TokenInfo', { location, history, match });
  const classes = useStyles();
  const theme = useTheme();

  const [loading, setLoading] = React.useState(true);
  const [sellBook, setSellBook] = React.useState([]);
  const [buyBook, setBuyBook] = React.useState([]);

  React.useEffect(() => {
    const opts = {
      query: { symbol },
    };
    Promise.all([
      findBuyBook(opts), //  
      findSellBook(opts), //  
    ]).then(([_buyBook, _sellBook]) => {
      // console.log({ _buyBook, _sellBook, _tradesHistory });
      const totalSellBook = _.sum(_sellBook.map(e => Number(e.quantity)));
      // console.log('totalSellBook', totalSellBook);
      const resultSellBook = _sellBook
        .map(e => {
          // const total = new BigNumber(e.quantity).times(e.price).toNumber();
          const quantity = parseFloat(e.quantity);
          return {
            account: e.account,
            // expiration: e.expiration,
            price: parseFloat(e.price),
            quantity,
            symbol: e.symbol,
            timestamp: e.timestamp,
            // txId: e.txId,
            // total,
            rate: (quantity / totalSellBook) * 100,
          };
        })
        .sort((a, b) => a.price - b.price);

      const totalBuyBook = _.sum(_buyBook.map(e => Number(e.quantity)));
      // console.log('totalBuyBook', totalBuyBook);
      const resultBuyBook = _buyBook
        .map(e => {
          // const total = new BigNumber(e.quantity).times(e.price).toNumber();
          const quantity = parseFloat(e.quantity);
          return {
            account: e.account,
            // expiration: e.expiration,
            price: parseFloat(e.price),
            quantity,
            // symbol: e.symbol,
            timestamp: e.timestamp,
            // txId: e.txId,
            // total,
            rate: (quantity / totalBuyBook) * 100,
          };
        })
        .sort((a, b) => b.price - a.price);

      setSellBook(resultSellBook);
      setBuyBook(resultBuyBook);
      setLoading(false);
    });
  }, []);

  return (
    <React.Fragment>
      <Grid container spacing={3}>
        <Grid item xs={6}>
          <div style={{ minHeight: 44, paddingLeft: 14 }}>
            <Typography variant="h6" id="tableTitle">
              Buy Orders
            </Typography>
          </div>
          <Divider />
          <div className={classes.tableWrapper}>
            {/* {loading ? <LinearProgress /> : null} */}
            <Table className={classes.table} size="small">
              <TableHead>
                <TableRow>
                  <TableCell>Account</TableCell>
                  <TableCell align="right">(STEEM)</TableCell>
                  <TableCell align="right">({symbol})</TableCell>
                  <TableCell align="right">(STEEM)</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {buyBook.map((e, index) => (
                  <TableRow key={String(index)}>
                    <TableCell component="th" scope="row">
                      {e.account}
                    </TableCell>
                    <TableCell align="center">
                      {new BigNumber(e.quantity).times(e.price).toFixed(5)}
                    </TableCell>
                    <TableCell align="right">{e.quantity.toFixed(3)}</TableCell>
                    <TableCell align="right">
                      <strong> {e.price.toFixed(5)}</strong>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
            {loading ? <LinearProgress /> : null}
          </div>
        </Grid>
        <Grid item xs={6}>
          <div style={{ minHeight: 44, paddingLeft: 14 }}>
            <Typography variant="h6" id="tableTitle">
              Sell Orders
            </Typography>
          </div>
          <Divider />
          <div className={classes.tableWrapper}>
            {/* {loading ? <LinearProgress /> : null} */}
            <Table className={classes.table} size="small">
              <TableHead>
                <TableRow>
                  <TableCell align="right">(STEEM)</TableCell>
                  <TableCell align="right">({symbol})</TableCell>
                  <TableCell align="right">(STEEM)</TableCell>
                  <TableCell>Account</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {sellBook.map((e, index) => (
                  <TableRow key={String(index)}>
                    <TableCell align="right" component="th" scope="row">
                      <strong>{e.price.toFixed(5)}</strong>
                    </TableCell>
                    <TableCell align="right">{e.quantity.toFixed(3)}</TableCell>
                    <TableCell align="center">
                      {new BigNumber(e.quantity).times(e.price).toFixed(5)}
                    </TableCell>
                    <TableCell>{e.account}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
            {loading ? <LinearProgress /> : null}
          </div>
        </Grid>
      </Grid>
    </React.Fragment>
  );
};

export default withConsumer(Orderbook);
