import React from 'react';
import './Orderbook.scss';

import classNames from 'classnames';
import BigNumber from 'bignumber.js';
import commaNumber from 'comma-number';

import LinearProgress from '@material-ui/core/LinearProgress';
import {
  getPrice,
  findTokens,
  findMarketMetrics,
  findBuyBook,
  findSellBook,
  findTradesHistory,
} from '../../service/SteemEngineService';

const Orderbook = props => {
  // console.log(props);
  const { params } = props.match;

  const [loading, setLoading] = React.useState(true);
  const [buyBook, setBuyBook] = React.useState([]);
  const [sellBook, setSellBook] = React.useState([]);

  React.useEffect(() => {
    const opts = {
      query: { symbol: params.symbol },
    };
    Promise.all([
      findBuyBook(opts), //  
      findSellBook(opts), //  
    ]).then(([_buyBook, _sellBook]) => {
      // console.log({ _buyBook, _sellBook });
      setBuyBook(
        _buyBook
          .map(e => ({
            account: e.account,
            price: parseFloat(e.price),
            quantity: parseFloat(e.quantity),
            txId: e.txId,
          }))
          .sort((a, b) => b.price - a.price),
      );
      setSellBook(
        _sellBook
          .map(e => ({
            account: e.account,
            price: parseFloat(e.price),
            quantity: parseFloat(e.quantity),
            txId: e.txId,
          }))
          .sort((a, b) => a.price - b.price),
      );
      setLoading(false);
    });
  }, []);

  return (
    <div className={classNames('page-orderbooks', 'page')}>
      <div className="crumb">
        <span>{params.market}</span> <span className="arrow"> &gt; </span>
        <span>{params.symbol}</span>
      </div>
      <div className="orderbooks-list">
        <div className={classNames('mod', 'bids')}>
          <div className="mod-head">Buy Orders</div>
          <div className="mod-body">
            <dl className="title">
              <dt /> <dd>({params.market})</dd>{' '}
              <dd>({params.symbol})</dd> <dd>Total ({params.market})</dd>
            </dl>
            {loading && <LinearProgress />}
            {buyBook.map(e => {
              return (
                <dl className="list" style={{ backgroundSize: '0% 100%' }}>
                  <dt className="color-buy">{e.account}</dt>
                  <dd>{e.price}</dd>
                  <dd>{e.quantity}</dd>
                  <dd>{new BigNumber(e.quantity).times(e.price).toFixed(6)}</dd>
                </dl>
              );
            })}
            {/* <dl className="list" style={{ backgroundSize: '0% 100%' }}>
              <dt className="color-buy">1</dt>
              <dd>9763.92</dd>
              <dd>0.023950</dd> <dd>0.023950</dd>
            </dl> */}
          </div>
        </div>
        <div className={classNames('mod', 'ask')}>
          <div className="mod-head">Sell Orders</div>
          <div className="mod-body">
            <dl className="title">
              <dt /> <dd>({params.market})</dd>{' '}
              <dd>({params.symbol})</dd> <dd>Total ({params.market})</dd>
            </dl>
            {loading && <LinearProgress />}
            {sellBook.map(e => {
              return (
                <dl className="list" style={{ backgroundSize: '0% 100%' }}>
                  <dt className="color-sell">{e.account}</dt>
                  <dd>{e.price}</dd>
                  <dd>{e.quantity}</dd>
                  <dd>{new BigNumber(e.quantity).times(e.price).toFixed(6)}</dd>
                </dl>
              );
            })}
            {/* <dl className="list" style={{ backgroundSize: '0% 100%' }}>
              <dt className="color-sell">1</dt>
              <dd>9763.92</dd>
              <dd>0.023950</dd> <dd>0.023950</dd>
            </dl> */}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Orderbook;
