import React from 'react';
import clsx from 'clsx';
import moment from 'moment';
import classNames from 'classnames';
import {
  fade,
  withStyles,
  makeStyles,
  useTheme,
} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Toolbar from '@material-ui/core/Toolbar';
import InputBase from '@material-ui/core/InputBase';
import SearchIcon from '@material-ui/icons/Search';
import TextField from '@material-ui/core/TextField';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Container from '@material-ui/core/Container';
import LinearProgress from '@material-ui/core/LinearProgress';

import SwipeableViews from 'react-swipeable-views';
import Typography from '@material-ui/core/Typography';
import BigNumber from 'bignumber.js';
import commaNumber from 'comma-number';
import { withConsumer } from '../../../Context';

import { findTradesHistory } from '../../../service/SteemEngineService';

const useStyles = makeStyles(theme => ({
  box: {
    // margin: theme.spacing(1),
    padding: theme.spacing(2),
    height: 'auto',
    display: 'block',
    marginBottom: 0,
  },
  root: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1),
  },
  spacer: {
    flex: '1 1 100%',
  },
  actions: {
    color: theme.palette.text.secondary,
  },
  title: {
    flex: '0 0 auto',
  },
  tableWrapper: {
    overflowX: 'auto',
  },
}));

const History = ({ symbol, context }) => {
  // console.log('TokenInfo', { location, history, match });
  const classes = useStyles();
  const theme = useTheme();

  const [loading, setLoading] = React.useState(true);
  const [tradesHistory, setTradesHistory] = React.useState([]);

  React.useEffect(() => {
    setLoading(true);
    const opts = {
      query: { symbol },
    };
    findTradesHistory(opts).then(_tradesHistory => {
      const resultTradesHistory = _tradesHistory
        .map(e => {
          return {
            // account: e.account,
            type: e.type,
            symbol: e.symbol,
            price: parseFloat(e.price),
            quantity: parseFloat(e.quantity),
            timestamp: e.timestamp,
            volume: e.volume,
          };
        })
        .sort((a, b) => b.price - a.price);
      setTradesHistory(resultTradesHistory);
      setLoading(false);
    });
  }, []);

  return (
    <React.Fragment>
      <Divider />
      <div className={classes.tableWrapper}>
        {/* {loading ? <LinearProgress /> : null} */}
        <Table className={classes.table} size="small">
          <TableHead>
            <TableRow>
              <TableCell></TableCell>
              <TableCell align="center">Type</TableCell>
              <TableCell align="right">(STEEM)</TableCell>
              <TableCell align="right">({symbol})</TableCell>
              <TableCell align="right">(STEEM)</TableCell>
              <TableCell align="right">(USD)</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {tradesHistory.map((e, index) => (
              <TableRow key={String(index)}>
                <TableCell component="th" scope="row">
                  {moment(new Date(e.timestamp * 1000)).format(
                    'YYYY-MM-DD HH:mm:ss',
                  )}
                </TableCell>
                <TableCell align="center">
                  <strong
                    style={{
                      color: `${e.type === 'buy' ? '#28a745' : '##dc3545'}`,
                    }}
                  >
                    {e.type.toUpperCase()}
                  </strong>
                </TableCell>
                <TableCell align="right">
                  <strong>{e.price.toFixed(6)}</strong>
                </TableCell>
                <TableCell align="right">{e.quantity.toFixed(6)}</TableCell>
                <TableCell align="right">
                  {new BigNumber(e.quantity).times(e.price).toFixed(6)}
                </TableCell>
                <TableCell align="right">
                  $
                  {new BigNumber(e.quantity)
                    .times(e.price)
                    .times(context.steemPrice)
                    .toFixed(6)}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
        {loading ? <LinearProgress /> : null}
      </div>
    </React.Fragment>
  );
};

export default withConsumer(History);
